/* Основные стили */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    transition: background 0.5s ease-in-out, color 0.3s ease-in-out;
}

/* Светлая тема */
body.light-theme {
    background: url("/public/images/gg.jpg") no-repeat center center fixed;
    background-size: cover;
}
/* Тёмная тема */
body.dark-theme {
    background: url("/public/images/гол.jpg") no-repeat center center fixed;
    background-size: cover;
}

/* Навигация */
.navbar {
    display: flex;
    justify-content: center;
    gap: 15px;
    padding: 20px;
}

.nav-btn {
    padding: 10px 15px;
    background-color: #008CBA;
    color: white;
    border-radius: 5px;
    text-decoration: none;
    transition: 0.3s;
}

.nav-btn:hover {
    background-color: #005f73;
}

/* Кнопка темы */
.theme-btn {
    padding: 10px;
    cursor: pointer;
    border: none;
    background-color: #222;
    color: white;
    border-radius: 5px;
    transition: 0.3s;
}

.theme-btn:hover {
    background-color: #555;
}

/* Страницы входа и регистрации */
.auth-page {
    text-align: center;
    padding: 50px;
}

.auth-page input {
    display: block;
    margin: 10px auto;
    padding: 10px;
    width: 200px;
}

.auth-page button {
    padding: 10px;
    background-color: green;
    color: white;
    border: none;
    cursor: pointer;
}
